import { axiosRequest } from './util';
export async function getSuggestions(partialQuery, controller){
    const baseURL = 'https://www.bayes.citystgeorges.ac.uk/webservices',
        dxpSuggestRootUrl = '/funnelback-dxp-suggest',
        maximumSuggestions = 10,
        timeout = 30000,
        collection = 'city~sp-bayes-main';
    // try {
    //     const response = await fetch(url + '&partial_query=' + partialQuery);
    //     if (!response.ok) {
    //     throw new Error(`Response status: ${response.status}`);
    //     }

    //     return await response.json();
    // } catch (error) {
    //     console.error(error.message);
    // }
    const config = {
        baseURL: baseURL,
        signal: controller.signal,
        url: dxpSuggestRootUrl,
        timeout: timeout,
        params: {
            collection: collection,
            partial_query: partialQuery,
            show: maximumSuggestions,
        },
    };

    return await axiosRequest(config);
}