import Handlebars from 'handlebars';
import { getSuggestions } from '../../js-utils/suggestions';


const className = 'header__site-search',
    modal = className + '__modal';


const template = Handlebars.compile('{{#each this}}<li role="option" id="city~sp-bayes-main--{{@index}}"><button type="button" data-suggestion="{{this.suggestion}}"><span class="sr-only">{{this.suggestion}}</span><span aria-hidden="true"><span class="header__site-search__modal__input__suggestions__key">{{this.searchTerm}}</span>{{this.sliceSearchTerm}}</span></button></li>{{/each}}');
let update = false;
let suggestionsModal = false; //boolean to indicate if modal added to DOM
let showSuggestions = false;
let controller; //signal controller

function inputKeyEventHandler (e){
    const searchInput = e.target,
        suggestion = searchInput.parentElement.querySelector('.header__site-search__modal__input__suggestions'),
        keyboardEvent = e.code;
    let suggestionFirstBtn = suggestion && suggestion.querySelector('li:first-of-type');

    switch (keyboardEvent){
        case 'ArrowDown':
            e.preventDefault();
            if(suggestion){
                searchInput.setAttribute('aria-activedescendant', suggestionFirstBtn.id);
                suggestionFirstBtn.querySelector('button').focus();
            }
            
            break;
        case 'ArrowUp':
            e.preventDefault();
            break;
        case 'Tab':
            
            if (suggestion && suggestion.classList.contains('show')){
                e.preventDefault();
                suggestion.classList.remove('show');
                searchInput.focus();
            }
            break;
    }
}

function suggestionClickEvent (e){
    const input = e.target.parentElement.parentElement.parentElement.querySelector('.header__site-search__modal__input');

    if (e.target.nodeName === 'BUTTON') {
        const searchModal = document.querySelector('.header__site-search__modal'),
            suggestion = searchModal.querySelector('.header__site-search__modal__input__suggestions'),
            suggestionBtn = searchModal.querySelectorAll('.header__site-search__modal__input__suggestions button');
        input.value = e.target.dataset.suggestion;
        suggestionBtn.forEach(btn => {
            btn.removeEventListener('click', suggestionClickEvent);
            btn.removeEventListener('keydown', suggestionKeyDown);
        });
        suggestionsModal && suggestion.remove();
        suggestionsModal = false;
        update = false;
        input.focus();
    }
}

function suggestionKeyDown (e){
    const suggestionElement = e.target.parentElement.parentElement,
        nextSiblingBtn = e.target.parentElement.nextElementSibling,
        previousSiblingBtn = e.target.parentElement.previousElementSibling,
        suggestionInput = e.target.parentElement.parentElement.previousElementSibling;

    const input = e.target.parentElement.parentElement.parentElement.querySelector('.header__site-search__modal__input');

        switch (e.code){
            case 'ArrowDown': 
                nextSiblingBtn && input.setAttribute('aria-activedescendant', nextSiblingBtn.id);
                nextSiblingBtn && e.target.blur();
                nextSiblingBtn && nextSiblingBtn.querySelector('button').focus();
                break;
            case 'ArrowUp':
                if(previousSiblingBtn){
                    previousSiblingBtn && input.setAttribute('aria-activedescendant', previousSiblingBtn.id);
                    previousSiblingBtn && e.target.blur();
                    previousSiblingBtn && previousSiblingBtn.querySelector('button').focus();
                }else{
                    e.target.blur();
                    showSuggestions = false;
                    input.focus();
                }
                
                break;
            case 'Tab': 
                e.preventDefault();
                showSuggestions = false;
                suggestionElement.classList.remove('show');
                suggestionInput.ariaExpanded = false;
                suggestionElement.ariaExpanded = false;
                suggestionInput.focus();
            break;
        }
}

function suggestionEvent (e){

    const searchInput = e.target,
        searchKeyLength = searchInput.value.length,
        searchValue = searchInput.value,
        suggestion = searchInput.parentElement.querySelector('.header__site-search__modal__input__suggestions'),
        keyboardEvent = e.code,
        navEvents = ['Tab', 'Enter', 'ArrowDown', 'ArrowUp', 'ArrowLeft', 'ArrowRight'],
        navStrokeEvent = navEvents.some( (el) => el === keyboardEvent);
    const suggestionWrapper = document.createElement('ul');
        suggestionWrapper.role = 'listbox';
        suggestionWrapper.className = 'header__site-search__modal__input__suggestions';
        suggestionWrapper.id = 'header__site-search__modal__input__suggestions';
        suggestionWrapper.ariaExpanded = true;

    // clean up
    if(controller){
        controller.abort(); //abort if a request is in progress
    }
    
    controller = new AbortController();

    if(suggestion){
        suggestion.querySelectorAll('.header__site-search__modal__input__suggestions button').forEach(btn => {
            btn.removeEventListener('click', suggestionClickEvent);
            btn.removeEventListener('keydown', suggestionKeyDown);
            });
        suggestion.remove();
    }
  
    showSuggestions && searchInput.classList.add('show');
    
    searchKeyLength && searchInput.classList.add('active');

    //Reset suggestion when open and user deletes search term
    if(!navStrokeEvent && searchKeyLength <= 2 && update && suggestionWrapper){
        update = false;

        suggestion && suggestion.querySelectorAll('.header__site-search__modal__input__suggestions button').forEach(btn => {
            btn.removeEventListener('click', suggestionClickEvent);
            btn.removeEventListener('keydown', suggestionKeyDown);
        });
        
        suggestionsModal && suggestion && suggestion.remove();
        suggestionsModal = false;
        showSuggestions = false;
    };
    

    if (searchKeyLength > 2){
        suggestionsModal = false;
        update = true;

        const suggestions = getSuggestions(e.target.value, controller);

        suggestions.then( response => {
            if(response){
                const responseObj = response.map( e => {
                    return {
                        suggestion: e,
                        searchTerm: searchValue,
                        sliceSearchTerm: e.slice(searchKeyLength)
                    };
                });
    
                return template(responseObj);
            }else{
                return null;
            }
        })
        .then( suggestionElement => {
            if(suggestionElement){
                suggestionWrapper.innerHTML = suggestionElement;
                suggestionWrapper.classList.add('show');
                e.target.offsetParent.querySelector('.header__site-search__modal__input').after(suggestionWrapper);
                suggestionsModal = true;
                showSuggestions = true;
            }
        })
        .finally( () => {
            const modalInput =  document.querySelector('.header__site-search__modal__input__wrapper'),
                suggestionBtn = modalInput.querySelectorAll('.header__site-search__modal__input__suggestions button'),
                input = modalInput.querySelector('.header__site-search__modal__input');

            if(suggestionBtn){
                input.setAttribute('aria-owns', 'header__site-search__modal__input__suggestions');
        
                suggestionBtn.forEach(btn => {
                    btn.addEventListener('click', suggestionClickEvent);

                    btn.addEventListener('keydown', suggestionKeyDown);
                });
            }  
        })
        .catch(error => {
            console.error('Error fetching suggestions:', error);
        });
    };
}
function launch(el) {

    const navButton = el.querySelector('a');

    const navSearchButton = document.createElement('button'),
        navSearchButtonWrapper = document.createElement('div'),
        navSearchButtonSpanText = document.createElement('span'),
        navSearchButtonSpanIcon = document.createElement('span');

        navSearchButtonWrapper.className = 'header__site-search__wrapper';

        navSearchButtonSpanText.className = 'header__site-search__nav-button__text';
        navSearchButtonSpanText.innerHTML = 'Search';

        navSearchButtonSpanIcon.className = 'header__site-search__nav-button__icon fa fa-search';
        navSearchButtonSpanIcon.ariaHidden = 'true';

        navSearchButton.className = 'header__site-search__nav-button';
        navSearchButton.type = 'button';
        navSearchButton.ariaExpanded = 'false';
        navSearchButton.ariaLabel = 'Open Search';

    const button = document.createElement('button'),
        buttonSpanWrapper = document.createElement('span'),
        textSpan = document.createElement('span'),
        iconSpan = document.createElement('span'),

        modalWrapper = document.createElement('div'),
        form = document.createElement('form'),
        inputFieldset = document.createElement('fieldset'),
        inputLabel = document.createElement('label'),
        input = document.createElement('input'),
        inputWrapper = document.createElement('div'),
        clearInputButton = document.createElement('button'),
        clearInputButtonIcon = document.createElement('span'),
        clearInputButtonSpanText = document.createElement('span');

    const overlay = document.createElement('div');

        overlay.className = 'search-overlay';

        modalWrapper.className = modal;
        form.action = '/search';
        form.method = 'GET';

        inputFieldset.className = modal + '__input-fieldset';
        
        inputLabel.htmlFor = modal + '__input';
        inputLabel.innerHTML = 'Search this website';
        inputLabel.className = modal + '__input-label sr-only';

        input.placeholder = 'Search the website e.g. Business';
        input.className = modal + '__input';
        input.id = modal + '__input';
        input.name =  'query';
        input.type = 'text';
        input.autocomplete = 'off';
        input.role = 'combobox';

        clearInputButton.className = modal + '__input__clear-button';
        clearInputButton.type = 'button';

        clearInputButtonIcon.className = modal + '__input__clear-button__icon fa fa-times';

        clearInputButtonSpanText.className = modal + '__input__clear-button__text sr-only';
        clearInputButtonSpanText.innerHTML = 'Clear';

        inputWrapper.className = modal + '__input__wrapper';

        button.className = modal + '__query__submit';
        button.type = 'submit';
        buttonSpanWrapper.className = modal + '__query__submit__content';
        iconSpan.className = modal + '__query__submit__icon fa fa-search';
        iconSpan.ariaHidden = 'true';
        textSpan.className = modal + '__query__submit__text sr-only';
        textSpan.innerHTML = 'Search';

        //events handlers
        clearInputButton.addEventListener('click', (e) =>{
            const input = e.target.parentElement.querySelector('#header__site-search__modal__input'),
                suggestions = e.target.parentElement.querySelector('.header__site-search__modal__input__suggestions');
            
            input.value = '';
            input.ariaExpanded = false;
            input.classList.remove('active');
            suggestions && suggestions.remove();
        });
        //global nav search button
        navSearchButtonWrapper.appendChild(navSearchButtonSpanText);
        navSearchButtonWrapper.appendChild(navSearchButtonSpanIcon);
        navSearchButton.appendChild(navSearchButtonWrapper);

        navButton.replaceWith(navSearchButton);

        //Search modal
        buttonSpanWrapper.appendChild(iconSpan);
        buttonSpanWrapper.appendChild(textSpan);
        button.appendChild(buttonSpanWrapper);

        //input clear button

        clearInputButton.appendChild(clearInputButtonIcon);
        clearInputButton.appendChild(clearInputButtonSpanText);

        inputWrapper.appendChild(inputLabel);
        inputWrapper.appendChild(input);
        inputWrapper.appendChild(clearInputButton);


        inputFieldset.appendChild(inputWrapper);
        inputFieldset.appendChild(button);
        form.appendChild(inputFieldset);

        modalWrapper.appendChild(form);

        el.lastChild.after(modalWrapper);

        el.parentElement.parentElement.after(overlay);

        const domModal = document.querySelector('.header__site-search__modal'),
            domNavButton = document.querySelector('.header__site-search__nav-button'),
            domModalInput = domModal.querySelector('.header__site-search__modal__input'),
            content = document.querySelector('.search-overlay');

        domNavButton.addEventListener('click', (e) => {
            const btn = e.currentTarget,
                icon = btn.querySelector('.header__site-search__nav-button__icon'),
                text = btn.querySelector('.header__site-search__nav-button__text');

            if (btn.ariaExpanded === 'true'){
                btn.ariaExpanded = 'false';
                btn.ariaLabel = 'Open search';
                icon.classList.remove('fa-times');
                icon.classList.add('fa-search');
                text.innerHTML = 'Search';
                content.classList.remove('search-overlay__active');
               
                e.target.focus();
            } else{
                btn.ariaExpanded = 'true';
                btn.ariaLabel = 'Close search';
                icon.classList.remove('fa-search');
                icon.classList.add('fa-times');
                text.innerHTML = 'Close';
                content.classList.add('search-overlay__active');

                domModalInput.focus();
            }
        });


        //Input events
        domModalInput.addEventListener('input', suggestionEvent);
        domModalInput.addEventListener('keydown', inputKeyEventHandler);
        domModalInput.addEventListener('focus', (event) => {
            const input = event.target,
                suggestion = input.nextElementSibling;
            
                if(suggestion && suggestion.classList.contains('header__site-search__modal__input__suggestions') && suggestion.classList.contains('show')){
                    suggestion.classList.add('show');
                    suggestion.ariaExpanded = true;
                }
        });

        //Suggestion btn events
        button.addEventListener('keydown', (event) => {
            if(!event.shiftKey && event.key === 'Tab'){
                const navBtn = event.target.parentElement.parentElement.parentElement.previousElementSibling,
                    icon = navBtn.querySelector('.header__site-search__nav-button__icon'),
                    text = navBtn.querySelector('.header__site-search__nav-button__text'); 

                    navBtn.ariaExpanded = 'false';
                    navBtn.ariaLabel = 'Open search';
                    icon.classList.remove('fa-times');
                    icon.classList.add('fa-search');
                    text.innerHTML = 'Search';
                    content.classList.remove('search-overlay__active');

            }
        });
}

export default {
    launch,
    className,
};